import React, { Component } from 'react';
import { Plot } from "./Plot.js"
import { Zip, ToHumanReadableBytes, COLORS } from "./Util.js"
// import Plot from 'react-plotly.js';
// import Chart from "react-apexcharts";
// import Math




//=================================================





class Server extends Component {
  FetchTimer = null;
  IntervalExpiredFor = 0;
  MaxDisplayTime = 100;
  PlotData = { time: 0, thr: {}, bytes: {} };
  ColorInputElements = {}

  constructor(props) {
    super(props)
    this.state = {
      play: true,
      plotDot: true,
      pltData: { time: 0, thr: {}, bytes: {} },
      ifcs: [],
      checked: {},
    }
  }



  componentDidMount() {
    this.setState({
      play: this.props.playAll ? this.props.playAll : false, //this.props.playAll,
      plotDot: this.props.plotDot ? this.props.plotDot : false, // this.props.plotDot
    })
    this.FetchTimer = setInterval((e) => this.OnTick(e), 1000)
  }

  componentWillUnmount() {
    clearTimeout(this.FetchTimer);
  }


  componentDidUpdate(prevProps, prevState, snapshot) {
    var tmpState = {}
    var updated = false
    if (this.props.playAll !== prevProps.playAll) {
      tmpState.play = this.props.playAll
      updated = true
    }
    if (this.props.plotDot !== prevProps.plotDot) {
      tmpState.plotDot = this.props.plotDot
      updated = true
    }
    if (updated) {
      this.setState(tmpState)
    }
  }

  OnTick(e) {
    if (!this.state.play)
      return
    this.IntervalExpiredFor += 1;

    fetch(this.props.server + "?" + this.IntervalExpiredFor)
      .then(r => r.json())
      .then(r => {
        // console.log(r)
        this.RecvData(r)
      })
    return null
  }


  RecvData(res) {
    var lastTime = this.PlotData.time
    var curTime = res['time']
    if (curTime <= lastTime)
      return
    var lastBytes = { ...this.PlotData.bytes }
    var curBytes = {}
    var curThr = {}
    for (var ifc in res["ifcs"]) {
      curBytes[ifc] = [res["ifcs"][ifc]['rx_bytes'], res["ifcs"][ifc]['tx_bytes']]

      var rxThr = 0;
      var txThr = 0
      if ((curTime - lastTime) <= this.MaxDisplayTime && lastBytes[ifc] != null) {
        rxThr = (curBytes[ifc][0] - lastBytes[ifc][0]) * 8 / (curTime - lastTime)
        txThr = (curBytes[ifc][1] - lastBytes[ifc][1]) * 8 / (curTime - lastTime)
        if(isNaN(rxThr) || isNaN(txThr)){
          return
        }
      }
      curThr[ifc] = [rxThr, txThr, curTime]
    }
    var newThr = {}
    // var curTimes = this.PlotData.times.filter(e => { return (curTime - e) <= this.MaxDisplayTime })
    // var discard = this.PlotData.times.length - curTimes.length
    for (ifc in curThr) {
      var datas = []
      if (this.PlotData.thr[ifc]) {
        datas = this.PlotData.thr[ifc].filter(e => { return (curTime - e[2]) <= this.MaxDisplayTime })
      }

      // if (curTimes.length !== datas.length) console.error("Lenght not matching")
      datas.push(curThr[ifc])
      newThr[ifc] = datas
    }
    // curTimes.push(curTime)
    var ifcs = Object.keys(newThr)
    var checked = { ...this.state.checked }
    ifcs.forEach((ifc, i) => {
      if (!(ifc in this.props.colors)) {
        var nextColor = COLORS[i % COLORS.length]
        this.props.onColorUpdate(ifc, nextColor)
      }
      if (!(ifc in this.state.checked)) {
        checked[ifc] = true
      }
    })

    this.PlotData = { thr: newThr, bytes: curBytes, time: curTime }
    this.setState({ pltData: this.PlotData, ifcs: ifcs, checked: checked })
  }

  //==========
  OnToggleChecked(ifc) {
    // console.log(e.target())
    var checked = { ...this.state.checked }
    checked[ifc] = this.state.checked[ifc] ? false : true
    this.setState({ checked: checked })
  }

  OnColorClicked(ifc) {
    if (this.ColorInputElements[ifc]) {
      this.ColorInputElements[ifc].click()
    }
  }

  ContainerCss = {
    margin: "5px",
    border: "2px solid black",
    padding: "5px",
    float: "left"
  }
  BlockViewCss = {
    display: "block",
    float: "left"
  }
  LegendBoxCss = {
    width: "40px",
    height: "20px"
  }
  IfcContCss = {
    marginLeft: "20px",
    display: "inline-block"
  }
  RxTxBytesHolderCss = {
    marginLeft: "20px",
    fontFamily: "'Courier New', Courier, monospace"
  }

  RenderPlot() {
    var rxData = []
    var txData = []
    var curTime = this.state.pltData.time
    var xrangeMin = this.props.xrangeMin ? 0 - this.props.xrangeMin : -10;
    var plotDot = this.state.plotDot; //this.props.plotDot ? this.props.plotDot : false;
    // var times = this.state.pltData.times.map(e => { return e - curTime })
    this.state.ifcs.forEach(ifc => {
      var color = this.props.colors[ifc]
      if (!(ifc in this.state.checked) || !this.state.checked[ifc]) {
        return
      }
      var xrangeMinTime = curTime + xrangeMin - 2
      var usableDt = this.state.pltData.thr[ifc].filter(e=>{return e[2] > xrangeMinTime})
      var td = Zip(usableDt)
      var rx = td[0]
      var tx = td[1]
      var tm = td[2].map(e => { return e - curTime })
      // console.log(rx, tx)
      rxData.push({
        data: [tm, rx],
        color: color,
      })
      txData.push({
        data: [tm, tx],
        color: color,
      })
    })
    return (
      <div>
        <Plot
          series={txData}
          width="500"
          height="200"
          xrange={[xrangeMin, 0]}
          points={plotDot}
          yTickFunc={e => ToHumanReadableBytes(e, 1)}
          xTickFunc={e => { return "" }}
          tickClass="plotTicks"
          yLabel="TX"
          spline={this.props.spline}
        />
        <Plot
          series={rxData}
          width="500"
          height="200"
          xrange={[xrangeMin, 0]}
          points={plotDot}
          yTickFunc={e => ToHumanReadableBytes(e, 1)}
          tickClass="plotTicks"
          // xLabel="Time"
          yLabel="RX"
          spline={this.props.spline}
        />
      </div>
    )
  }

  RenderIfc(ifc, id) {
    var color = this.props.colors[ifc]
    return (
      <div style={this.BlockViewCss} key={id}>
        <div style={this.IfcContCss}>
          <input type="checkbox" style={{ float: "left" }} checked={this.state.checked[ifc] ? "checked" : ""} readOnly onClick={e => this.OnToggleChecked(ifc)} />
          <div style={this.BlockViewCss}>{ifc}:</div>
          {/* <div
            onClick={e => this.OnColorClicked(ifc)}
            style={{ ...this.LegendBoxCss, ...this.BlockViewCss, background: color + " none repeat scroll 0% 0%", marginLeft: "20px" }}
          > */}
            <input
              type="color"
              onChange={(e) => { this.props.onColorUpdate(ifc, e.currentTarget.value) }}
              // ref={input => this.ColorInputElements[ifc] = input}
              // style={{ visibility: "hidden" }}
              style={{ ...this.LegendBoxCss, ...this.BlockViewCss, background: color + " none repeat scroll 0% 0%", marginLeft: "20px", border: 0, padding: 0 }}
              data-clr-query="server1591503971937"
              data-clr-ifc="wlp7s0"
              value={color}
            />
          {/* </div> */}
        </div>
        <div style={this.RxTxBytesHolderCss}>
          <b>↑</b>TX:<span style={{ color: color, whiteSpace: "pre" }}>{ToHumanReadableBytes(this.PlotData.bytes[ifc][1])+"B"}</span>
        </div>
        <div style={this.RxTxBytesHolderCss}>
          <b>↓</b>RX:<span style={{ color: color, whiteSpace: "pre" }}>{ToHumanReadableBytes(this.PlotData.bytes[ifc][0])+"B"}</span>
        </div>
      </div>
    )
  }

  render() {

    var playBtnVal = this.state.play ? (<>&#9646;&#9646;</>) : (<>&#9654;</>)
    return (
      <div style={this.ContainerCss} ref={this.props.fref}>
        <span style={{ float: "left", cursor: "pointer", color: "green" }} onClick={(e) => { if (this.props.onInfoClick) this.props.onInfoClick() }}>🛈</span>
        <span style={{ float: "left", cursor: "pointer", padding: "0 5px" }} onClick={(e) => this.setState({ play: !this.state.play })}>{playBtnVal}</span>
        <span style={{ float: "left", cursor: "pointer", padding: "0 5px" }} onClick={(e) => this.setState({ plotDot: !this.state.plotDot })}>&#x25EF;</span>
        <span style={{ float: "right", cursor: "pointer", color: "red" }} onClick={e => { if (this.props.onClose) this.props.onClose() }}>✖</span>
        <center>
          <h2 style={{ margin: "0" }}>{this.props.name}</h2>
        </center>
        <table style={{ width: (this.props.width ? this.props.width : 500) + "px" }}>
          <tbody>
            <tr>
              <td style={{verticalAlign: "middle", width: "20px", writingMode: "vertical-lr", transform: "rotate(180deg)"}}>Speed (bps)</td>
              <td>
                {this.RenderPlot()}
              </td>
            </tr>
            <tr>
              <td></td>
              <td style={{textAlign: "center", verticalAlign: "top", padding:"0 0 10px 0"}}>
                Time (sec)
              </td>
            </tr>
            <tr>
              <td colSpan={2}>
                {this.state.ifcs.map((ifc, id) => this.RenderIfc(ifc, id))}
                <br/>
                &nbsp;
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}
Server.defaultProps = {
  displayTime: 10,
  playAll: true,
  plotDot: false
}


export {
  Server
};
