import React, { Component } from 'react';
import { Zip, CreateInterpolant } from "./Util.js"
// import Spline  from "cubic-spline"


function SelectAxesTicks(range, max = 4) {
    var diff = range[1] - range[0]
    var tick = CalcStepSize(diff, max) //Math.ceil(diff/(max))
    var minTick = Math.ceil(tick * Math.ceil(range[0] / tick))
    var ticks = [minTick]
    while (minTick < range[1]) {
        minTick += tick
        ticks.push(minTick)
    }
    return ticks;
}

const CalcStepSize = function (range, targetSteps) {
    // calculate an initial guess at step size
    var tempStep = range / targetSteps;

    // get the magnitude of the step size
    var mag = Math.floor(Math.log(tempStep) / Math.LN10);
    var magPow = Math.pow(10, mag);

    // calculate most significant digit of the new step size
    var magMsd = Math.round(tempStep / magPow + 0.5);

    // promote the MSD to either 1, 2, or 5
    if (magMsd > 5.0)
        magMsd = 10.0;
    else if (magMsd > 2.0)
        magMsd = 5.0;
    else if (magMsd > 1.0)
        magMsd = 2.0;

    return magMsd * magPow;
};

class Plot extends Component {
    BaseViewBox = [0, -10, 514, 137]


    PolyLines = []
    Curves = []
    SpPaths = []
    Texts = []
    Dots = []
    ViewBox = this.BaseViewBox
    NYRange = [2, 102]
    NXRange = [52, 502]
    Axes = [
        {
            points: [
                [
                    [this.NXRange[0], this.NYRange[0]],
                    [this.NXRange[0], this.NYRange[1]],
                    [this.NXRange[1], this.NYRange[1]],
                    [this.NXRange[1], this.NYRange[0]],
                    [this.NXRange[0], this.NYRange[0]]
                ]
            ],
            attr: { stroke: "black", strokeWidth: 1 }
        }
    ]
    LastInstances = {}

    SelectRanges() {
        var xrange = []
        var yrange = []
        var xrangeInProp = false
        var yrangeInProp = false

        if (this.props.xrange && this.props.xrange.length === 2) {
            xrange = this.props.xrange
            xrangeInProp = true
        }
        if (this.props.yrange && this.props.yrange.length === 2) {
            yrange = this.props.yrange
            yrangeInProp = true
        }

        var selectedPoints = this.props.series.map(ele => {
            return Zip(ele.data).filter(e => {
                return ((!xrangeInProp) || (e[0] >= xrange[0] && e[0] <= xrange[1]))
                    && ((!yrangeInProp) || (e[1] >= yrange[0] && e[1] <= yrange[1]))
            })
        })

        selectedPoints.forEach(ele => {
            var xvals = ele.map(e => e[0])
            var yvals = ele.map(e => e[1])

            xvals.push(...xrange)
            yvals.push(...yrange)
            if (!xrangeInProp) xrange = [Math.floor(Math.min(...xvals)), Math.ceil(Math.max(...xvals))]
            if (!yrangeInProp) yrange = [Math.floor(Math.min(...yvals)), Math.ceil(Math.max(...yvals))]
        })

        if (xrange[0] === xrange[1]) {
            xrange = [xrange[0] - 1, xrange[1] + 1]
        }

        if (yrange[0] === yrange[1]) {
            yrange = [yrange[0] - 1, yrange[1] + 1]
        }

        return [xrange, yrange]
    }

    SetupTicksAndGrids(xrange, yrange) {
        this.SpPaths = this.Axes.slice()
        // this.ViewBox = this.BaseViewBox.slice()

        var yTickFunc = this.props.yTickFunc ? this.props.yTickFunc : e => { return e }
        var xTickFunc = this.props.xTickFunc ? this.props.xTickFunc : e => { return e }
        var tickClass = this.props.tickClass ? this.props.tickClass : ""
        var xTickClass = this.props.xTickClass ? this.props.xTickClass : tickClass
        var yTickClass = this.props.yTickClass ? this.props.yTickClass : tickClass

        var xGridLines = []
        var xTickLines = []
        var xTexts = []
        var yViewBox = [this.BaseViewBox[1], this.BaseViewBox[3]]
        if (this.LastInstances.xrange && xrange[0] === this.LastInstances.xrange[0] && xrange[1] === this.LastInstances.xrange[1]) {
            xGridLines = this.LastInstances.xGridLines
            xTexts = this.LastInstances.xTexts
            xTickLines = this.LastInstances.xTickLines
            yViewBox = this.LastInstances.yViewBox
        }
        else {
            // console.log("xrange === this.LastInstances.xrange", xrange, this.LastInstances.xrange)
            var xticks = SelectAxesTicks(xrange, 10)
            var ystart = this.NYRange[1] + 4
            var yend = this.NYRange[1]
            xticks.forEach(x => {
                var nx = (x - xrange[0]) * (this.NXRange[1] - this.NXRange[0]) / (xrange[1] - xrange[0]) + this.NXRange[0]
                xTickLines.push([[nx, ystart], [nx, yend]])
                xGridLines.push([[nx, yend], [nx, this.NYRange[0] - 2]])
                xTexts.push({ attr: { x: nx, y: ystart + 14, textAnchor: "middle", className: xTickClass }, val: xTickFunc(x) })
            })
            if (this.props.xLabel) {
                const [x, y] = [(this.NXRange[0] + this.NXRange[1]) / 2, ystart + 28]
                xTexts.push({
                    attr: {
                        x: x,
                        y: y,
                        textAnchor: "middle",
                        className: xTickClass,
                    },
                    val: this.props.xLabel
                })
                yViewBox[1] += 14
            }
            this.LastInstances.xrange = xrange
            this.LastInstances.xGridLines = xGridLines
            this.LastInstances.xTexts = xTexts
            this.LastInstances.xTickLines = xTickLines
            this.LastInstances.yViewBox = yViewBox
        }

        var yGridLines = []
        var yTickLines = []
        var yTexts = []
        var xViewBox = [this.BaseViewBox[0], this.BaseViewBox[2]]
        if (this.LastInstances.yrange && yrange[0] === this.LastInstances.yrange[0] && yrange[1] === this.LastInstances.yrange[1]) {
            yGridLines = this.LastInstances.yGridLines
            yTexts = this.LastInstances.yTexts
            yTickLines = this.LastInstances.yTickLines
            xViewBox = this.LastInstances.xViewBox
        }
        else {
            // console.log("yrange === this.LastInstances.yrange", yrange, this.LastInstances.yrange)
            var yticks = SelectAxesTicks(yrange)
            var xstart = this.NXRange[0] - 4
            var xend = this.NXRange[0]
            yticks.forEach(y => {
                var ny = this.NYRange[1] - ((y - yrange[0]) * (this.NYRange[1] - this.NYRange[0]) / (yrange[1] - yrange[0])) //SVG y is reversed
                if (ny < this.NYRange[0]) return
                yTickLines.push([[xstart, ny], [xend, ny]])
                yGridLines.push([[xend, ny], [this.NXRange[1] + 2, ny]])
                yTexts.push({ attr: { x: xstart - 2, y: ny + 4, textAnchor: "end", className: yTickClass }, val: yTickFunc(y) })
            })
            if (this.props.yLabel) {
                const [x, y] = [0, (this.NYRange[0] + this.NYRange[1]) / 2]
                yTexts.push({
                    attr: {
                        x: x,
                        y: y,
                        textAnchor: "middle",
                        className: yTickClass,
                        transform: "rotate(270, " + x + "," + y + ")"
                    },
                    val: this.props.yLabel
                })
                xViewBox[0] -= 10
            }
            this.LastInstances.yrange = yrange
            this.LastInstances.yGridLines = yGridLines
            this.LastInstances.yTexts = yTexts
            this.LastInstances.yTickLines = yTickLines
            this.LastInstances.xViewBox = xViewBox
        }


        this.SpPaths.push({ points: xTickLines.concat(yTickLines), attr: { stroke: "black", strokeWidth: 2 } })
        this.SpPaths.push({ points: xGridLines.concat(yGridLines), attr: { stroke: "gray", strokeWidth: 1 } })
        this.Texts = xTexts.concat(yTexts)
        this.ViewBox = [xViewBox[0], yViewBox[0], xViewBox[1], yViewBox[1]]
    }

    PreparePlots() {
        var ployLines = [] //this.Axes.slice()
        var curves = []
        var dots = []

        var [xrange, yrange] = this.SelectRanges()
        this.SetupTicksAndGrids(xrange, yrange)
        // ployLines.push(...ticksGrid)

        this.props.series.forEach((ele, i) => {
            if (ele.length <= 1)
                return
            var points = Zip(ele.data)
            var npoints = points.slice()

            npoints = npoints.map(ele => {
                var x = ele[0]
                var y = ele[1]
                var nx = (x - xrange[0]) * (this.NXRange[1] - this.NXRange[0]) / (xrange[1] - xrange[0]) + this.NXRange[0]
                var ny = this.NYRange[1] - ((y - yrange[0]) * (this.NYRange[1] - this.NYRange[0]) / (yrange[1] - yrange[0])) //SVG y is reversed
                return [nx, ny]
            })
            if (this.props.points) {
                npoints.forEach(pt => {
                    if (pt[0] < this.NXRange[0] || pt[0] > this.NXRange[1] || pt[1] < this.NYRange[0] || pt[1] > this.NYRange[1])
                        return
                    dots.push({
                        attr: {
                            cx: pt[0],
                            cy: pt[1],
                            r: 3,
                            fill: "white",
                            stroke: ele.color ? ele.color : "#ff00ff",
                            strokeWidth: ele.strokeWidth ? ele.strokeWidth : "2",
                        }
                    })
                })
            }
            if (this.props.spline) {
                var [xs, ys] = Zip(npoints)
                const spline = CreateInterpolant(xs, ys);
                npoints = []
                for (let i = this.NXRange[0]; i <= this.NXRange[1]; i += 5) {
                    var y = spline(i)
                    npoints.push([i, y])
                }
            }
            curves.push({
                points: npoints,
                attr: {
                    fill: "none",
                    stroke: ele.color ? ele.color : "#ff00ff",
                    strokeWidth: ele.strokeWidth ? ele.strokeWidth : "2",
                },
                curve: true,
            })
        })
        this.PolyLines = ployLines
        this.PolyLines.push(...curves)
        this.Dots = dots
        // this.Curves = curves
    }


    render() {
        // console.log("plot key:", this.props.key)
        this.PreparePlots()
        var clip = {
            x: this.NXRange[0],
            y: this.NYRange[0],
            width: this.NXRange[1] - this.NXRange[0],
            height: this.NYRange[1] - this.NYRange[0],
        }
        var lid = 0;
        return (
            <svg viewBox={this.ViewBox.join(" ")}>
                <clipPath id="shape">
                    <rect {...clip} />
                </clipPath>
                {
                    this.SpPaths.map((pth, id) => {
                        var points = []
                        pth.points.forEach(path => {
                            points.push(["M"])
                            points.push(path[0].join(" "))
                            points.push("L")
                            var cpt = path.slice(1).map(pt => {
                                return pt.join(" ")
                            }).join(", ")
                            points.push(cpt)
                        })
                        lid += 1
                        return (
                            <path
                                // clipPath="url(#shape)"
                                key={lid}
                                {...pth.attr}
                                fill="none"
                                // stroke={pth.color}
                                // strokeWidth={pth.strokeWidth}
                                d={points.join(" ")}
                            />
                        )
                    })
                }
                {

                    this.Texts.map((txt, i) => {
                        lid += 1
                        return (
                            <text key={lid} {...txt.attr}>
                                {txt.val}
                            </text>)
                    })
                }
                {
                    this.PolyLines.map((line, id) => {
                        var cPathId = ""
                        if (line.curve)
                            cPathId = "url(#shape)"
                        lid += 1
                        return (
                            <polyline
                                clipPath={cPathId}
                                key={lid}
                                fill="none"
                                {...line.attr}
                                // stroke={line.color}
                                // strokeWidth={line.strokeWidth}
                                points={line.points.map(p => { return p.join(",") }).join(" ")}
                            />
                        )
                    })
                }
                {
                    this.Curves.map((crv, id) => {
                        var points = ["M"]
                        points.push(crv.points[0].join(" "))
                        points.push("A")
                        var cpt = crv.points.slice(1).map(pt => {
                            return pt.join(" ")
                        }).join(", ")
                        points.push(cpt)
                        lid += 1
                        return (
                            <path
                                clipPath="url(#shape)"
                                key={lid}
                                fill="none"
                                {...crv.attr}
                                // stroke={crv.color}
                                // strokeWidth={crv.strokeWidth}
                                d={points.join(" ")}
                            />
                        )
                    })
                }
                {
                    this.Dots.map((crv, id) => {
                        lid += 1
                        return (
                            <circle
                                // clipPath="url(#shape)"
                                key={lid}
                                {...crv.attr}
                            />
                        )
                    })
                }
            </svg>
        )
    }

}

export {
    Plot
}