import React, { Component } from 'react';
class Modal extends Component {
  RenderCloseButton() {
    if (this.props.closeButton)
      return <span className="modal-basic-close" onClick={this.props.onClose}>&times;</span>
  }

  shouldComponentUpdate() {
    return false
  }

  render() {
    // console.log(this.props.children)
    if (this.props.open) {
      return (
        <div className="modal-basic">
          <div className="modal-basic-content">
            {this.RenderCloseButton()}
            {this.props.children}
          </div>
        </div>
      )
    }
    return (<></>)
  }
}

Modal.defaultProps = {
  closeButton: true,
  open: true
}


function ConfirmModal(props) {
  return (
    <Modal open={true} onClose={(e) => props.onClose(e)}>
      <table width="100%">
        <tbody>
          <tr><td colspan="2">{props.children}</td></tr>
          <tr>
            <td align="right" width="50%">
              <button
                name="No"
                style={{ backgroundColor: "green" }}
                onClick={e => props.onNo(e)}>No</button>
            </td>
            <td align="left">
              <button
                name="Yes"
                style={{ backgroundColor: "red", }}
                onClick={e => props.onYes(e)}>Yes</button>
            </td>
          </tr>
        </tbody>
      </table>
    </Modal>
  )
}

function ModalFormInput(props) {
  return (
    <tr>
      <td align="right" width="50%">{props.label}</td>
      <td align="left"><input defaultValue={props.defaultValue} name={props.name} type={props.type ? props.type : "text"} /></td>
    </tr>
  )
}

function ModalForm(props) {
  return (
    <Modal open={true} onClose={(e) => props.onClose(e)}>
      <form onSubmit={(e) => props.onSubmit(e)}>
        <table width="100%">
          <tbody>
            {props.children}
            <tr>
              <td colSpan="2" align="center"><input className="button" type="submit" value="Submit" /></td>
            </tr>
          </tbody>
        </table>
      </form>
    </Modal>
  )
}


export {
  Modal,
  ConfirmModal,
  ModalForm,
  ModalFormInput
}