import React, { Component } from 'react';
import { Server } from "./Components.js";
import { ConfirmModal, ModalForm, ModalFormInput } from "./Modal.js"
import ReactFullscreen from 'react-easyfullscreen';

class App extends Component {
    ImportElement = null

    constructor(props) {
        super(props);
        this.Widths = [200, 500, 900]
        this.state = {
            addNewServerModal: false,
            addThisServerModal: false,
            playAll: true,
            xrangeMin: 10,
            editServerIndexModal: -1,
            deleteServerIndexModal: -1,
            importConfigModal: false,
            removeAllServersModal: false,
            spline: false,
            width: this.Widths[1],
            serverStates: this.restoreServerStateFromLocalStorage(),
        }
    }

    addServer(server, name, id) {
        var curServer = {
            id: id,
            name: name,
            server: server,
            colors: {} //per ifc diff color
        }

        this.setState({
            serverStates: [...this.state.serverStates, curServer]
        })
    }

    updateServer(server, name, id) {
        var curServer = {
            id: id,
            name: name,
            server: server,
            colors: {} //per ifc diff color
        }
        this.setState({
            serverStates: this.state.serverStates.map((s, i) => {
                return id === i ? curServer : s;
            })
        })
    }

    restoreServerStateFromLocalStorage() { //should run only during constructor
        if (typeof (Storage) !== "undefined") {
            if (localStorage.serverStates) {
                return JSON.parse(localStorage.serverStates)
            }
        }
        return []
    }

    storeServerStateToLocalStorage() { //should run only during constructor
        if (typeof (Storage) !== "undefined") {
            localStorage.serverStates = JSON.stringify(this.state.serverStates)
        }
    }

    componentDidUpdate(_, prevState) {
        if (this.state.serverStates !== prevState.serverStates) {
            this.storeServerStateToLocalStorage()
            // console.log("componentDidUpdate", this.state.serverStates === prevState.serverStates, this.state, prevState)
        }
    }
    //=======================================================
    //Handlers
    //=======================================================

    handleLoadConfig(event) {
        var input = event.target
        var reader = new FileReader();
        reader.onload = () => {

            var text = reader.result;
            var jsonData = JSON.parse(text)
            this.setState({ serverStates: jsonData, importConfigModal: false })
            // location.reload()
        };
        reader.readAsText(input.files[0]);
    }

    handleNewServerSubmitted(e) {
        e.preventDefault()
        var frmData = new FormData(e.target);
        var server = frmData.get("addr")
        var name = frmData.get("name")
        if (server === "" || server === null || name === "" || name === null) {
            alert("invalid input")
            return
        }
        if (!server.startsWith("https://") && !server.startsWith("http://")) {
            server = "http://" + server
        }
        var date = new Date()
        var id = date.getTime()
        this.addServer(server, name, id)
        // add2Storage(server, name, id)
        this.setState({ addNewServerModal: false })
    }

    handleEditServerSubmitted(e) {
        e.preventDefault()
        var frmData = new FormData(e.target);
        var server = frmData.get("addr")
        var name = frmData.get("name")
        if (server === "" || server === null || name === "" || name === null) {
            alert("invalid input")
            return
        }
        if (!server.startsWith("https://") && !server.startsWith("http://")) {
            server = "http://" + server
        }
        var id = this.state.editServerIndexModal
        this.updateServer(server, name, id)
        // add2Storage(server, name, id)
        this.setState({ editServerIndexModal: -1 })
    }

    handleUpdateColor(index, ifc, color) {
        // console.log(index, ifc, color)
        var serverStates = this.state.serverStates.map((s, i) => {
            if (index === i) {
                s.colors[ifc] = color
            }
            // colors = {...s.colors}
            return s
        })
        this.setState({ serverStates: serverStates })
    }

    //=======================================================
    //Renders
    //=======================================================

    renderNewServerModal() {
        // console.log("Rendering modal")

        if (this.state.addNewServerModal) {
            return (
                <ModalForm
                    onClose={e => this.setState({ addNewServerModal: false })}
                    onSubmit={e => this.handleNewServerSubmitted(e)}
                >
                    <ModalFormInput label="Name:" name="name" />
                    <ModalFormInput label="Address:" name="addr" />
                </ModalForm>
            )
        }
        if (this.state.addThisServerModal) {
            var date = new Date()
            var id = date.getTime()
            this.addServer("/", "Current", id)
            this.setState({ addThisServerModal: false })
        }
    }

    renderServerInfoModal() {
        // console.log("Rendering modal")
        if (this.state.editServerIndexModal > -1 && this.state.editServerIndexModal < this.state.serverStates.length) {
            var curServer = this.state.serverStates[this.state.editServerIndexModal]
            return (
                <ModalForm
                    onClose={e => this.setState({ editServerIndexModal: -1 })}
                    onSubmit={e => this.handleEditServerSubmitted(e)}
                >
                    <ModalFormInput label="Name:" name="name" defaultValue={curServer.name} />
                    <ModalFormInput label="Address:" name="addr" defaultValue={curServer.server} />
                </ModalForm>
            )
        }
    }

    renderConfigLoadModal() {
        if (this.state.importConfigModal) {
            return (
                <ConfirmModal
                    onClose={(e) => this.setState({ importConfigModal: false })}
                    onYes={e => { if (this.ImportElement) this.ImportElement.click() }}
                    onNo={e => { this.setState({ importConfigModal: false }) }}
                >
                    Are you sure? This action will wipe current layout and load the stored one.
                </ConfirmModal>
            )
        }
    }

    renderDeleteServerModal() {
        if (this.state.deleteServerIndexModal >= 0 && this.state.deleteServerIndexModal < this.state.serverStates.length) {
            return (
                <ConfirmModal
                    onClose={(e) => this.setState({ deleteServerIndexModal: -1 })}
                    onYes={e => { this.setState({ deleteServerIndexModal: -1, serverStates: this.state.serverStates.filter((e, i) => { return i !== this.state.deleteServerIndexModal }) }) }}
                    onNo={e => { this.setState({ deleteServerIndexModal: -1 }) }}
                >
                    Are you sure? This action will wipe current layout and load the stored one.
                </ConfirmModal>
            )
        }
    }

    renderRemoveAllServerModal() {
        if (this.state.removeAllServersModal) {
            return (
                <ConfirmModal
                    onClose={(e) => this.setState({ v: false })}
                    onYes={e => { this.setState({ serverStates: [], removeAllServersModal: false }) }}
                    onNo={e => { this.setState({ removeAllServersModal: false }) }}
                >
                    Are you sure? This action will wipe current layout and load the stored one.
                </ConfirmModal>
            )
        }
    }

    renderServer(s, id) {
        return (
        <ReactFullscreen onChange={(e) => {
            console.log("Full Screen Changed", e)
            }}>
            {({ ref, onToggle }) => (
                <Server
                    fref={ref}
                    onFToggle={onToggle}
                    xrangeMin={this.state.xrangeMin}
                    key={"server:" + id}
                    playAll={this.state.playAll}
                    plotDot={this.state.plotDot}
                    {...s}
                    spline={this.state.spline}
                    onColorUpdate={(ifc, c) => { this.handleUpdateColor(id, ifc, c) }}
                    onInfoClick={() => this.setState({ editServerIndexModal: id })}
                    onClose={() => { this.setState({ deleteServerIndexModal: id }) }}
                    width={this.state.width}
            />)}
        </ReactFullscreen>
        )
    }

    render() {
        // console.log("Rendering")
        var curve = (<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 -10 190 130" style={{width:"19px", height:"11px"}}><path fill="None" stroke="#000" d="M10 60 C 40 -10, 65 -10, 95 60 S 150 130, 180 60" stroke-width="20" ></path></svg>)
        var sharp = (<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 -10 190 130" style={{width:"19px", height:"11px"}}><path fill="None" stroke="#000" d="M 10 115 L 50 5, 140 115, 180 5" stroke-width="20"></path></svg>)
        var playBtnVal = this.state.playAll ? (<>&#9646;&#9646;</>) : (<>&#9654;</>)
        var splineBtnVal = this.state.spline ? curve : sharp
        var magChar = (<span role="img" aria-label="save">&#x1F50D;</span>)
        return (
            <>
                {this.renderNewServerModal()}
                {this.renderServerInfoModal()}
                {this.renderConfigLoadModal()}
                {this.renderDeleteServerModal()}
                {this.renderRemoveAllServerModal()}
                <div>
                    <div className="controlBarLeft">
                        <button title="Add this server" onClick={(e) => this.setState({ addThisServerModal: true })}>+</button>
                        <button title="Add a new server" onClick={(e) => this.setState({ addNewServerModal: true })}>&#x2605;</button>
                        <button title="Toggle play/pause" onClick={(e) => this.setState({ playAll: !this.state.playAll })}>{playBtnVal}</button>
                        <button title="Toggle dot in plot" onClick={(e) => this.setState({ plotDot: !this.state.plotDot })}>&#x25EF;</button>
                        <button title="Toggle spline in plot" onClick={(e) => this.setState({ spline: !this.state.spline })}>{splineBtnVal}</button>

                        <button title="Zoom-out" onClick={(e) => this.setState({ width: this.state.width - 10 })} disabled={this.state.width <= this.Widths[0]}>{magChar}-</button>
                        <button title="neutral" onClick={(e) => this.setState({ width: this.Widths[1] })} disabled={this.state.width === this.Widths[1]}>{magChar}</button>
                        <button title="Zoom-in" onClick={(e) => this.setState({ width: this.state.width + 10 })} disabled={this.state.width >= this.Widths[2]}>{magChar}+</button>

                        <select onChange={(e) => { var tr = e.target; this.setState({ xrangeMin: tr.options[tr.selectedIndex].value }) }}>
                            <option value="10">10 Sec</option>
                            <option value="20">20 Sec</option>
                            <option value="30">30 Sec</option>
                            <option value="40">40 Sec</option >
                            <option value="50">50 Sec</option >
                        </select >
                        {/* =========== */}
                        {/* Close all button */}
                        {/* =========== */}
                        <button title="Remove all servers" onClick={(e) => this.setState({ removeAllServersModal: true })} style={{color: "red"}}>&#x2716;</button>
                    </div >
                    <div className="controlBarRight">
                        <a download="config.json" href={"data:json/application;charset=utf-8," + encodeURIComponent(JSON.stringify(this.state.serverStates))}>
                            <button title="Export current config">
                                <span role="img" aria-label="save">&#x1F4BE;</span>
                            </button>
                        </a>
                        <button title="Import previously stored config" onClick={e => { this.setState({ importConfigModal: true }) }}>
                            <span role="img" aria-label="save">&#x1f4c2;</span>
                        </button>
                    </div>


                    <div style={{ clear: "both" }}></div>
                </div>
                {/* <div className="clear">
                </div> */}
                <div id="demotitle" ><center><h1 style={{ margin: 0 }}>Remote Network Activity Monitor</h1></center></div>
                <div className="servers">
                    {this.state.serverStates.map((a, b, c) => { return this.renderServer(a, b, c); })}
                </div>
                <input ref={e => this.ImportElement = e} type="file" accept="application/json" onChange={e => { this.handleLoadConfig(e) }} style={{ display: "none" }} />
            </>
        );
    }
}

export default App;
